@import 'myAntd.css';
.m-login-wrap{display: flex; position: absolute;top: 0;left: 0;right: 0;bottom: 0;background: url(../images/m-login-bg.jpg) no-repeat center;}
.m-login{margin: auto;padding: 26px 16px 0;width: 400px;height: 239px;background: url(../images/m-loginform-bg.png) 100% 100%;}
.m-login-row{display: flex; margin: 20px;}
.m-login-icon{width: 40px;font-size: 26px;color: #555;}
.m-login-btn-wrap{margin: 20px; padding: 0 0 0 36px;}
.m-loading{display: none; position: fixed;top: 0;left: 0;right: 0;bottom: 0;background: rgba(0, 0, 0, 0);}
/* .m-loading::before{margin: auto; content: '';display: inline-block;width: 38px;height: 38px;background-image: url(../images/loading.png);animation: loading 0.5s linear infinite;} */
.m-loading.active{display: flex;}
.m-spin{margin: auto;}
.m-index-wrap{display: flex; position: absolute;top: 0;left: 0;right: 0;bottom: 0;}
.m-list{display: flex;flex-direction: column; position: relative;flex:1;overflow: hidden;}
.m-sidebar-icon.big{font-size: 22px!important;margin: 0 0 0 -4px;}
.m-sidebar-icon-wrap{vertical-align: text-bottom;}
.m-logo{height: 64px;line-height: 64px;text-align: center;font-size: 16px;font-weight: bold; color: white;border-bottom: 1px solid #333;}
.m-logo.light{color: rgba(0, 0, 0, 0.6);border-bottom: 1px solid #ddd;}
.m-sidebar{display: none;}
.m-sidebar.active{display: block;}
.m-list-header{display: flex; height: 64px;line-height: 64px;border-bottom: 1px solid #ddd;}
.m-list-header-icon{display: inline-block; width: 52px;height: 64px;font-size: 30px;cursor: pointer;text-align: center;}
.m-list-header-icon.rotate{transform: rotate(180deg);}
.m-list-header-icon:hover{background: #ddd;}
.m-list-info{display: flex;flex-direction: column; flex: 1;overflow-y: auto;}
.m-list-header-info{flex: 1}
.m-list-header-menu{width: 200px;}
.m-list-header-menu-welcome{margin: 0 20px 0 0;}
.m-list-header-menu-username{margin: 0 5px;cursor: pointer;}
.m-list-header-menu-theme{margin: 0 0 0 20px;cursor: pointer;}
.m-list-nav{padding: 9px 0 0 20px; height: 40px;border-bottom: 1px solid #ddd;}
.m-empty-page{flex:1;line-height: 300px; text-align: center;color: #ddd;font-size: 50px;overflow-y: auto;background-color: #f0f2f5;}
.m-login-footer{position: absolute;bottom: 0;left: 0;right: 0; line-height: 70px;text-align: center;border-top: 1px solid #e8e8e8;color: white; font-size: 12px; background-color: #426374;}
.m-content-wrap{position: relative; display: flex;flex-direction: column; flex:1;overflow-y: auto;background-color: #f0f2f5;}
.m-content-table{margin: 5px 10px 5px;flex: 1;}
.m-content-box{margin: 10px 10px 5px;padding: 10px; background: white;}
.m-content-tool{margin: 5px 10px 5px;padding: 10px; background: white;}
.m-company-img{width: 60px;height: 60px;}
.m-check-status{display: inline-block;width: 60px;text-align: center;border-radius: 4px;color: white;background: #3bb4f2;font-weight: bold;font-size: 12px;line-height: 18px;}
.m-check-status.checking{background: #3bb4f2;}
.m-check-status.pass{background:#5eb95e;}
.m-check-status.no-pass{background: #e6e6e6;color: #333;}
.m-check-status.open{background: #5eb95e;}
.m-check-status.forbidden{background: #f37b1d;}
.m-check-status.never{background: #333;}
.m-org{color: #f37b1d;}
.m-action{text-align: left;}
.m-action-btn{margin: 0 4px 4px 0;}
.m-action-btn.open{color: green;}
.m-action-btn.open:hover,.m-action-btn.open:focus,.m-action-btn.open:active{border-color: green;}
.m-action-btn.forbidden{color: #f37b1d;}
.m-action-btn.forbidden:hover,.m-action-btn.forbidden:focus,.m-action-btn.forbidden:active{border-color: #f37b1d;}
.m-content-search{margin: 10px 10px 5px;padding: 10px; background: white;}
.m-btn{margin: 0 5px;}
.m-content-tool-row{margin: 5px 0;}
.m-tool-btn-icon{vertical-align: top;font-size: 14px;margin: 0 2px 0 0;}
.m-tool-btn-icon.rotate90{transform: rotate(90deg);}
.m-tool-btn-icon.rotate_90{transform: rotate(-90deg);}
.m-space{margin: 0 8px 0 0;}
.m-table-shop-count{color: red;}
.m-table-serach{display: none;}
.m-table-serach.active{display: block;}
.m-baidu-map-link a{color: red;}
.m-upload-text{margin: 0 0 0 10px;color: #999;}
.m-wrok-day-select{width: 100px!important;}
.m-wrok-day-text{margin: 0 5px;}
.m-search-operate{text-align: left;}
.m-search-operate.active{text-align: right;}
.m-template-page{margin: 10px 10px 5px;padding: 10px; background: white;text-align: center;line-height: 50px;font-size: 30px;color: #ddd;}
.m-total-text{font-weight: bold;}
.m-input-footer-msg{color: red;}
.m-form-item-msg{color: red;}
.m-form-item-msg-bold{font-weight: bold;text-decoration: underline;}
.m-table-link{text-decoration: underline;cursor: pointer;}
.m-modal-check-msg{font-size: 16px;}
.m-modal-check-msg.pass{color: green;}
.m-modal-check-msg.checking{color:#3bb4f2 ;}
.m-home-welcome{font-size: 20px;line-height: 32px;color: #333;margin: 0 0 10px 10px;}
.m-modal-form-info{position: relative;}
.m-category-icon{display: inline-block; margin: 0 0 0 5px;font-size: 20px;cursor: pointer;vertical-align: top;}
.m-category-item{display: block; margin: 0 0 5px 0;}
.m-form-order{width: 150px;}










@keyframes loading {
  0% {transform: rotate(0deg);}
  100% { transform: rotate(360deg);}
}