@import '~antd/dist/antd.css';
.m-my-table .ant-table{font-size: 12px;}
.m-my-table .ant-table-thead .ant-table-cell{white-space: nowrap;}
.m-modal-full-screen{position: fixed;margin: 0; padding: 0; top: 0;left: 0;right: 0;bottom: 0px;width: 100%!important;max-width: 100%;}
.m-modal-full-screen .ant-modal-content{position: relative; width: 100%;height: 100%;}
.m-modal-full-screen .ant-modal-body{height: calc(100% - 110px); overflow-y: auto;}
.m-modal-full-screen .ant-form{margin: 20px 0 20px 0;}
.m-modal-full-screen .m-modal-footer{position: absolute;left: 0;right: 0;bottom: -24px;padding: 0 20px; height: 55px;line-height: 55px;background: white;  border-top: 1px solid #ddd;}
.ant-menu-submenu-selected .m-sidebar-icon.big{font-weight:1000;}

